@keyframes blink {
    50% {
        background-color: transparent;
    }
}

.onEvent {
    z-index: 10000;
    height: 180px;
    width: 525px;
    background-color: #9f3737;

    animation: blink 1s;
    animation-iteration-count: 30;

    border-radius: 10px;
    padding: 10px 10px 10px 10px;
    margin-top: 1rem;
    border: 3px solid #7b0000;

}

