/* 흐림 배경 - 혼잡도 */

.modal_content_observer_crowdDensityService {
	min-width: 82vw;
	height: 78vh;
	background-color: rgba(0, 0, 0, 0.433);
	/* backdrop-filter: blur(8px); */
	border: 1.5px solid;
	border-color: #8e8e8e;
	border-radius: 20px;
	box-shadow: 5px 5px 15px 4px black;

	background-image: url("../../assets/images/dashboard/modal_background.png");
    background-position: center; 


}

.body {
	display: flex;
	flex-wrap: wrap;


    margin-left: 1.5rem;

}

.content {
	width: 100%;
	height: 70.856vh;
	margin: auto;
	display: flex;
	flex-direction: column;
}

.navItem_menu {
	display: flex;
	flex-direction: column;
	margin-top: 4vh;
}

.navItem {
	width: 120%;
}

.addCamera {
	position: absolute;
	right: 0.6vw;
}

.observer_crowd_col_sm_1 {
	padding: 0;
	width: 100%;
}

.observer_crowd_col_sm_11 {
	max-width: 64vw;
	display: flex;
	justify-content: center;
	height: 51vh;
}

.setting_modal_closer {
	display: flex;
	justify-content: flex-end;
	text-decoration: none;
	position: absolute;
	top: 0px;
	right: 10px;
	color: rgb(255, 255, 255);
	padding: 0.3rem;
	cursor: pointer;
	z-index: 1;
	margin-top: 7px;
	margin-right: 2px;
}

.setting_modal_closer:after {
	display: flex;
	align-content: center;
	content: "✖";
	font-size: 20pt;
	color: #fff;
	cursor: pointer;
	z-index: 1;
}

.cameraDetail_content {
	position: relative;
	bottom: 5%;
	width: 45%;
}

.crowd_density_camera_title {
	font-weight: 700;
	font-size: large;
	position: fixed;
	top: 11vh;
	border-radius: 6px;
	border: 1.2px solid transparent;
	background-origin: border-box;
	background-clip: content-box, border-box;
	background-image: linear-gradient(#4e4e4e, #000000), linear-gradient(208deg, #383838 10%, #b9b9b9 80%, #595757);
	width: 9%;
	height: 3.5%;
	align-items: center;
	display: flex;
	justify-content: center;
	letter-spacing: 0.2rem;
	margin-bottom: 0;
}

.crowdDensity_chart_area {
	width: 100%;
	margin-right: 1rem;
	border-radius: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 50px;
	height: 21vh;
	margin-top: 27px;
	background-color: rgb(51, 51, 51);
}