.parking_status_table {
	width: 100%;
}

.parking_status_all {
	display: flex;
    border-bottom: 1.5px solid #4e4e4e;
    text-align: center;
    border-image: linear-gradient(to right, #000000 0%, #aaa7a7, #000000 100%);
    border-image-slice: 1;
	margin: 15px 0px 10px 0px;
	padding-bottom: 10px;
}

.type_detail_row {
	display: flex;

	padding-bottom: 3px;
}

.parking_type_label {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 4.5vh;
	font-size: 1.2rem;
	border-radius: 1rem;
	margin: 0.5rem 1rem;
	width: 60%;

	box-shadow: inset 2px 2px 2px #00000085;
    border: 1px solid #5f5f5f;	
	text-shadow: 2.5px 2.5px 2px black;

}

.parking_type_label_all{
	background-color: #474747;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 4.5vh;
	font-size: 1.2rem;
	border-radius: 1rem;
	margin: 0.5rem 1rem;
	width: 60%;

	box-shadow: inset 2px 2px 2px #00000085;
    border: 1px solid #5f5f5f;	
	text-shadow: 2.5px 2.5px 2px black;
}

.parking_type_icon {
	margin-right: 1rem;
	text-shadow: 2.5px 2.5px 2px black;

}

.parking_type_text{
	text-shadow: 2.5px 2.5px 2px black;
}



