.close {
  text-decoration: none;
  position: absolute;
  color: rgb(192, 192, 192);
  top: 2px;
  right: 8px;
  padding: 0.3rem;
}

.indication_line_default {
  border-top: 20px solid #000000d5;
  border-left: 50px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 0px solid transparent;
  content: "";
  position: absolute;
  top: 25px;
  left: -50px;
}

.indication_line_left {
  /* border-top: 20px solid #272F42; */
  border-top: 20px solid #000000d5;
  border-left: 50px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 0px solid transparent;
  content: "";
  position: absolute;
  top: 25px;
  right: -50px;
  transform: rotateY(180deg);
}

.indication_line_top {
  /* border-top: 20px solid #272F42; */
  border-top: 20px solid #000000d5;
  border-left: 50px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 0px solid transparent;
  content: "";
  position: absolute;
  bottom: 25px;
  left: -50px;
  transform: rotateY(0deg) rotateX(180deg)
}

.indication_line_left_top {
  /* border-top: 20px solid #272F42; */
  border-top: 20px solid #000000d5;
  border-left: 50px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 0px solid transparent;
  content: "";
  position: absolute;
  bottom: 25px;
  right: -50px;
  transform: rotateY(180deg) rotateX(180deg);
}

.close:after {
  content: "✖";
  color: rgb(291, 291, 291);
}

