.parentDiv {
	position: relative;
	width: 100%;
}

.parentDiv_all {
	position: relative;
	width: 50%;
	height: 33.4365322vh;
}

.childDiv {
	position: relative;
	display: flex;
	width: 60vw;
	height: 67.3003029vh;
	margin: 0;
}

.childDiv_all {
	position: relative;
	display: flex;
	width: 29vw;
	height: 32.4365322vh;
	margin: 0;
	top: -32.4365322vh;
}

.delete_btn {
	/* text-align: center;
	padding: 0.2rem 0.4rem;
	border-radius: 0.5rem; */
	position: absolute;
	top: -5.5vh;
	right: 0vw;
	cursor: pointer;
	text-align: center;
	padding: 0.2rem 0.4rem;
	border-radius: 0.5rem;
	font-weight: 700;
	text-shadow: 1px 1px #eae7e7;
	color: #101010;
	background: linear-gradient(133deg, #424242 -10%, #c2c2c2 30%, #c2c2c3 60%, #424242);
	border: 1.5px solid #bbbaba;
	box-shadow: 1px 1px 3px 1px black;
	margin-top: 8px;
	margin-left: -0.5rem;
}

.delete_btn:hover {
	box-shadow: 0px 0px 2px 2.2px #a9c3d3;
	border: 1.5px solid #474646;
}

.delete_btn:active {
	background: linear-gradient(133deg, #000000 -10%, #494848 30%, #787878 60%, #101010);
	text-shadow: 1px 1px #252525;
	border: 1.5px solid #0b0b0b;
	box-shadow: 0px 0px 2px 2.2px #4f4f4f;
	color: #b1b1b1;
}