.status {
  position: relative;
  left: 50%;
  z-index: 1;
}

.location {
  /* display: flex;
  position: relative;
  bottom: 1rem;
  width: 40%;
  justify-content: space-around;
  border: 2px solid; */

  color: #e2e2e2;
  display: flex;
  position: relative;
  bottom: 1rem;
  width: 40%;
  justify-content: space-around;
  border-radius: 0.5rem;
  border: 1px solid transparent;
  background-origin: border-box;
  background-clip: content-box, border-box;
  background-image: linear-gradient(#000000, #000000), linear-gradient(171deg, #555454, #9c9898, #606060);
  font-size: 1.15rem;
  margin-top: 45px;
  margin-left: 110px;
  height: 2rem;
  font-weight: 500;
}

.edit {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.edit:hover {
  color: cornflowerblue;
  transform: scale(1.1);
}

.occupancy,
.in_count,
.out_count {
  display: flex;
  align-items: center;
}