.parkingCamera_title {
	margin-top: 0.6rem;
	font-weight: 700;
	font-size: x-large;
	position: fixed;
	top: 125px;
	border-radius: 6px;
	border: 1.2px solid transparent;
	background-origin: border-box;
	background-clip: content-box, border-box;
	background-image: linear-gradient(#4e4e4e, #000000), linear-gradient(208deg, #383838 10%, #b9b9b9 80%, #595757);
	width: 9%;
	height: 3.5%;
	align-items: center;
	display: flex;
	justify-content: center;
	letter-spacing: 0.2rem;
}

.parking-tab.row {
	display: flex;
	flex-direction: row;
	min-width: 1800px;
}

.setting_modal_closer {
	display: flex;
	justify-content: flex-end;
	text-decoration: none;
	position: absolute;
	top: 0px;
	right: 10px;
	color: rgb(255, 255, 255);
	padding: 0.3rem;
	cursor: pointer;
	z-index: 1;
	margin-top: 7px;
	margin-right: 2px;
}

.setting_modal_closer:after {
	display: flex;
	align-content: center;
	content: "✖";
	font-size: 20pt;
	color: #fff;
	cursor: pointer;
	z-index: 1;
}

.modal_content_observer_parkingService {
	min-width: 1800px;
	height: 93vh;
	background-color: rgba(0, 0, 0, 0.433);
	/* backdrop-filter: blur(8px); */
	border: 1.5px solid;
	border-color: #8e8e8e;
	border-radius: 20px;
	box-shadow: 5px 5px 15px 4px black;
}

.parking_status {
	padding-bottom: 0.5rem;
	border-bottom: 2px solid #8e8e8e;
}

.parkingCamera_wrapper {
	display: flex;
	/* justify-content: space-around; */
	align-items: center;
	height: 70vh;
	width: 100%;
	flex-wrap: wrap;
	margin-bottom: 60px;
	margin-top: 60px;
}

