@font-face {
  font-family: 'KorailRoundGothicBold';
  src: local('KorailRoundGothicBold'), url('../font/Korail_Round_Gothic_Bold.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
/* 흐림 배경 - 레이아웃 설정 */

.CustomLayout {
  width: 22.875rem;
  /* background-color: #000000ba; */
  position: absolute;
  top: 35%;
  left: 40%;
  z-index: 100;
  background: url("../../assets/images/dashboard/modal_background.png");
  background-position: center;
  /* backdrop-filter: blur(5px); */
  border: 1.5px solid #a8a8a8;
  border-radius: 10px;
  box-shadow: 2px 2px 10px 5px black;
  font-family: 'KorailRoundGothicBold';
}

.header {
  margin: 0.75rem;
}

.closeBtn {
  display: flex;
  justify-content: flex-end;
  text-decoration: none;
  position: absolute;
  top: 0px;
  right: 10px;
  color: rgb(255, 255, 255);
  padding: 0.3rem;
  cursor: pointer;
  z-index: 1;
}

.closeBtn:after {
  display: flex;
  align-content: center;
  content: "✖";
  font-size: 20pt;
  color: #fff;
  cursor: pointer;
  z-index: 1;
}

.leftbar {
  padding: 0.15rem;
  border: 1px solid #d8d8d8;
  border-radius: 5%;
}

.rightbar {
  padding: 0.15rem;
  border: 1px solid #d8d8d8;
  border-radius: 5%;
}

.leftbar_label {
  width: 100%;
  display: flex;
  justify-content: center;
}

.rightbar_label {
  width: 100%;
  display: flex;
  justify-content: center;
}

.displayLayout {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 1rem;
}

.layout_select {
  background-color: #000000e0;
  color: white;
  border: 1.5px solid gray;
  margin: 4px;
  border-radius: 10px;
  height: 2rem;
  width: 8rem;
  margin: 6px;

}