  border: 1.5px solid #626262;
  width: 8rem;
  margin-left: -26px;
  border-radius: 10px;
  background-color: #323232;
  text-shadow: 2px 2px 2px black;
  text-align: center;
  height: 2rem;
  /* box-shadow: inset -1px -1px 7px 0px black; */
  font-weight: 800;
  font-size: large;
  padding-top: 0.07rem;
