.type_filter_select {
  color: white;
  height: 2rem;
  margin-top: 2rem;
  width: 8rem;
  font-weight: 800;
  border-radius: 10px;
  /* background-color: #323232; */
  text-shadow: 2px 2px 2px black;
  text-align: center;
  /* box-shadow: inset -1px -1px 7px 0px black; */
  font-weight: 800;
  font-size: large;
  letter-spacing: 0.7rem;
  margin-right: -1.4rem;
  padding-left: 1rem;
  cursor: pointer;
  margin-left: 92.5px;
  background: linear-gradient(314deg, #424040, #7b7777);
  border-color: #9ea1ab;
  box-shadow: inset -2px -1px 9px 2px #717171;
  box-shadow: inset -1px -1px 7px 0px black;
}

.option {
  background-color: #272727;
}

.type_filter_select_all {
  color: white;
  height: 1.6rem;
  width: 7rem;
  border-radius: 6px;
  /* background-color: #323232; */
  text-shadow: 2px 2px 2px black;
  text-align: center;
  /* box-shadow: inset -1px -1px 7px 0px black; */
  font-weight: 500;
  font-size: medium;
  letter-spacing: 0.5rem;
  cursor: pointer;
  padding: 2px 0px 2px 0px;
  margin-top: -0.05rem;
  margin-left: 0.5rem;
  padding-left: 1rem;


  background: linear-gradient(314deg, #424040, #7b7777);
  border-color: #9ea1ab;
  box-shadow: inset -2px -1px 9px 2px #717171;
  box-shadow: inset -1px -1px 7px 0px black;
}


