  height: 30.6vh;
  position: fixed;
  top: 260px;
  width: 26rem;
}

.setting_space_type {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-around;
  margin-top: 0.8rem;
}

.setting_space_type_space {
  /* width: 40%;
  height: 90%;
