  width: 40%;
  height: 90%;
  border-radius: 0.5rem;
  border: 1.5px solid transparent;
  background-image: linear-gradient(#000000, #000000), linear-gradient(316deg, #c8c8c8, #272727);
  background-origin: border-box;
  background-clip: content-box, border-box;

}

.setting_space_type_type {
  width: 40%;
  height: 90%;
  border-radius: 0.5rem;
