.unRegisteredList {
  list-style: none;
  height: 19vh;
  margin-bottom: 0;
  margin-right: 0.3rem;
  overflow-y: auto;
}

.unRegisteredList::-webkit-scrollbar {
  width: 9px;
}

.unRegisteredList::-webkit-scrollbar-thumb {
  background-color: #2f3542;
  border-radius: 10px;
  background-clip: padding-box;
  border: 2px solid transparent;
}

.unRegisteredList::-webkit-scrollbar-track {
  background-color: grey;
  border-radius: 10px;
  box-shadow: inset 0px 0px 5px white;
}

.unRegisteredDetail {
  display: flex;
  justify-content: space-around;
}

.unRegisteredDetail_label {
  margin-bottom: 0;
}

.registeredList {
  list-style: none;
  height: 19vh;
  margin-bottom: 0;
  margin-right: 0.3rem;
